import React from 'react';
import Footer from './Footer';
import { Chip } from '@mui/material';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import db from 'libs/db';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import CurrencyInput from 'react-currency-input-field';

export default function LifeInsurance(props) {

	const [lifeInsurance, setLifeInsurance] = React.useState("");
	const STATE = props.location.state

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "0", "name": "No life insurance" },
		{ "key": "15000000", "name": "1.5 Crore" },
		{ "key": "20000000", "name": "2 Crore" },
	]

	React.useEffect(async ()=>{
		await db.get(db.model.riskProfile).then(p=>{
			if(p){
				setLifeInsurance(p.lifeInsurance || "")
			} else {
				setLifeInsurance("")
			}
		})
		setLoading(false)
	},[])

	const checkValidation = () => {
		if (lifeInsurance) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [lifeInsurance])

	if (STATE === undefined) {
		props.history.push('/Dashboard')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Identifying your risk profile"
				navigation={props.history}
			/>
			<ProgressBar now={45} className="progress-bar-custom" />
			<div className="dataContainer" style={{paddingTop:'42px'}}>
				{
					loading ?
					<ActivityLoaderMintbox />
						:
						<>
							<div className="header1dark">What is the size of your life insurance cover?</div>
							<CurrencyInput
									className='normalInput rupee'
									id="lifeInsurance"
									name="lifeInsurance"
									placeholder="1,00,00,000"
									value={lifeInsurance}
									allowDecimals={false}
									allowNegativeValue={false}
									disableAbbreviations={false}
									autoFocus
									prefix=' '
									onValueChange={(value) => setLifeInsurance(value)}
									intlConfig={{ locale: 'en-IN', currency: 'INR' }}
								/>
							<div className='hiddden-scrollbar'>
								{chipset.map((item) => {
									return (
										<Chip
											className={lifeInsurance===item.key?"normalChip selected":"normalChip"}
											key={item.key}
											label={item.name}
											onClick={() => {
												setLifeInsurance(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/risk-profile/6"
				state={{...STATE,lifeInsurance}}
				saveData={{ lifeInsurance }}
				buttonText="Confirm"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
